import {
  React, lazy, Link,
  Panel,
  PanelBody,
  PanelHeader,
  connect, Swal,
  ModalGlobal,
  hideModal, showModal,
  ToastNotification,
  postData2
} from "../../../components/helpers/library";
import { setHancurBarang } from "../../../actions/databarang_action.jsx";
import OtorisasiHancur from "./OtorisasiHancur.jsx";
import { deleteImage, getImage } from "../../../components/helpers/function";
const HeadaHancurDataBarang = lazy(() => import("./HeadaHancurDataBarang.jsx"));
const maptostate = (state) => {
  return {
    setHancurBarang: state.databarang.setHancurBarang,
  };
};

class FormHancurBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      isLoadingOtorisasi: false,
      columns: [
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          footerAttrs: {
            colSpan: "5",
          },
          footer: (cell, row, num, index) => {
            return (
              <div>
                Total Barang :{" "}
                {this.props.setHancurBarang.length === 0
                  ? JSON.parse(
                      localStorage.getItem("data_barang_hancur_tmp")
                    ) === null
                    ? 0
                    : JSON.parse(localStorage.getItem("data_barang_hancur_tmp"))
                        .length || 0
                  : this.props.setHancurBarang.length || 0}{" "}
              </div>
            );
          },
        },

        {
          dataField: "kode_group",
          text: "Gudang Asal",
        },
        {
          dataField: "kode_dept",
          text: "Baki Asal",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "nama_atribut",
          text: "Nama Atribut",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return cell.toFixed(3);
          },
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0).toFixed(3),
        },
        {
          dataField: "kondisi_barang",
          text: "Kondisi Barang",
          footer: "",
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.hapusdata(index)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let data = JSON.parse(localStorage.getItem("data_barang_hancur_tmp"));
        data.splice(id, 1);
        localStorage.setItem("data_barang_hancur_tmp", JSON.stringify(data));
        this.props.dispatch(setHancurBarang(data));
        // deleteLocalItemBarcode("data_barang_hancur_tmp", id);
        // this.props.dispatch(getListReturnSupplier());
      }
    });
  }

  handleSubmit(e) {
    this.props.dispatch(showModal());
  }

  hancurBarang(data) {
    this.setState({
      isLoadingOtorisasi: true,
    });
    let hasil = {
      user_id: data.username,
      password: data.password,
      kategori: "HANCUR BARANG",
      description: "SIMPAN HANCUR BARANG",
      keterangan: data.keterangan.toUpperCase() || "-",
    };

    
    // return false;
    postData2("authorization", hasil)
      .then((resposn) => {
        ToastNotification("success", "Otorisasi Berhasil");

        this.setState({ isLoading: true });
        let data =
          this.props.setHancurBarang.length === 0
            ? JSON.parse(localStorage.getItem("data_barang_hancur_tmp")) || 0
            : this.props.setHancurBarang || 0;
        // console.log(data);
        let hasil = {
          detail_barang: data,
        };
        // console.log(data);
        // return false
        postData2("hancurbarang/add", hasil)
          .then(async() => {
            for (let row of data) {
              getImage('foto_produk/'+row.kode_barcode).then((res)=>{
                deleteImage('foto_produk/'+row.kode_barcode).then((res)=>{
                }).catch((err)=>{
                  this.setState({ isLoading: false, isLoadingOtorisasi: false });
                  ToastNotification("info",'Terjadi Kesalahan Saat Hancur Barang Silahkan Ulangi Kembali')
                  return false
                })
              }).catch((err)=>{
                console.clear();
              })
            }
            this.setState({ isLoading: false, isLoadingOtorisasi: false });
          })
          .then(() => {
            localStorage.setItem("data_barang_hancur_tmp", JSON.stringify([]));
          })
          .then(() => {
            this.props.dispatch(setHancurBarang([]));
          })
          .then(() => {
            this.setState({
              isLoadingOtorisasi: false,
            });
            this.props.dispatch(hideModal());
            ToastNotification("success", "Data Berhasil Dihancur");
          })
          .catch((error) => {
            this.setState({
              isLoading: false,
              isLoadingOtorisasi: false,
            });
            this.errorhandling(error);
          });
      })
      .catch((err) => {
        ToastNotification("info", err.response?.data?.message || "Terjadi Kesalhan Saat Mengirim Data");
        this.setState({
          isLoading: false,
          isLoadingOtorisasi: false,
        });
      });
  }
  errorhandling(error) {
    ToastNotification(
      "info",
      /conflict/i.test(error.response?.data.message)
        ? "Terjadi Kesalahan Saat Mengirim Data"
        : error.response?.data.message || "Terjadi Kesalahan Saat Mengirim Data"
    );
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Barang</Link>
          </li>
          <li className="breadcrumb-item active">Hancur Barang</li>
        </ol>
        <h1 className="page-header">Hancur Barang </h1>
        <Panel>
          <PanelHeader>Hancur Barang</PanelHeader>
          <br />

          {/* Hancur Barang */}
          <PanelBody>
            <HeadaHancurDataBarang
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
              columns={this.state.columns}
            />
          </PanelBody>
          <br />
          {/* End Hancur Barang */}

          {/* End Tambah Hancur Barang  */}
          <ModalGlobal
            size="L"
            title="Otorisasi Hancur Barang"
            content={
              <OtorisasiHancur
                keterangan
                isLoadingOtorisasi={this.state.isLoadingOtorisasi}
                onSubmit={(data) => this.hancurBarang(data)}
              />
            }
          />
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormHancurBarang);
